@use 'theme';
@use 'variables';

*,
*:before,
*:after {
  user-select: none;

  -webkit-box-sizing: border-box;
     -moz-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  position: relative;
  min-width: 1240px;
  overflow-x: hidden;
}

body::-webkit-scrollbar{
  display: none;
}

.draggable-area {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 50px;
  -webkit-app-region: drag;
}

button, input {
  font: inherit;
  color: inherit;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: 0;
  outline: none;
  text-align: left;
}

:root {
  background-color: theme.$background;
}

@keyframes launch-animation {
  0% {
    filter: blur(7px);
  }

  100% {
    opacity: 1;
  }
}
